<template>
  <section class="bg-white p-3 rounded-md">
    <PageTitle>
      Markazdan chiqarilgan bolalarni monitoring qilish
    </PageTitle>
    <section>
      <TableTeenagersForSelectWrapper v-model:selected-juvenile="selectedJuvenile" :update-juveniles-list="updateJuvenilesList" :fetch-juveniles-list-handler="fetchAllocatedJuvenilesList" />
      <span class="error_message">{{ selectedJuvenileError }}</span>
    </section>
    <section v-if="selectedJuvenile" class="my-4">
      <h1 class="text-2xl my-4">
        Tanlangan bolaning shaxsiy ma'lumotlari
      </h1>
      <div class="flex">
        <PassportCard
          :passport-info="selectedJuvenile"
          @canceled="setSelectedJuvenile(null)"
        />
      </div>
    </section>
    <section v-if="selectedJuvenile">
      <MonitoringForm :clear-form-mode="clearFormFields" @submit="handleSubmit" />
    </section>
  </section>
</template>

<script setup>
import MonitoringForm from "../components/MonitoringForm";
import TableTeenagersForSelectWrapper from "../components/Tables/TableTeenagersForSelectWrapper";
import PassportCard from "../components/PassportCard";
import {ref, watch} from "vue";
import useAlert from "../composables/useAlert";
import PageTitle from "../components/LayoutItems/PageTitle";
import AllocateService from "../services/allocate.service";
import MonitoringService from "../services/monitoring.service";
import useErrorAlert from "../composables/useErrorAlert";

const {fetchAllocatedJuvenilesList} = AllocateService

const selectedJuvenile = ref(null)
const selectedJuvenileError = ref('')
const clearFormFields = ref(false)

const {alertIt} = useAlert()
const {alertThisError} = useErrorAlert()
const {addJuvenileToMonitoring} = MonitoringService

const updateJuvenilesList = ref(false)
const refreshJuvenileList = ()=>{
  updateJuvenilesList.value = true
}

watch(()=>selectedJuvenile.value,(val)=>{
  if(val && selectedJuvenileError.value){
    selectedJuvenileError.value = ''
  }
})

function clearAllFields(){
  selectedJuvenile.value = null
  clearFormFields.value = true
}


function setSelectedJuvenile(juvenile){
  selectedJuvenile.value = juvenile
}

async function handleSubmit(formData){
  try {
    if(!selectedJuvenile.value){
      selectedJuvenileError.value = 'Bola tanlanishi shart!'
      return null
    }
    const { message } = (await addJuvenileToMonitoring(formData,selectedJuvenile.value.id)).data
    await alertIt(message,'success')
    clearAllFields()
    refreshJuvenileList()
  } catch (e) {
    alertThisError(e)
  }
}

</script>

