<template>
  <TableView v-model:current-page="currentPage" v-model:page-size="pageSize" :all-pages-count="allPagesCount" :page-size-options="pageSizeOptions" @table-options-changed="getJuvenileList">
    <template #filter-additional-content>
      <div class="w-full flex justify-between items-center">
        <div class="grid grid-cols-2 md:grid-cols-4 gap-4 2xl:w-[60%]">
          <div class="form_wrapper my-3">
            <input autofocus v-validate-cyrillic v-model="full_name" class="base_input" placeholder="F.I.O qidirish" type="text" />
          </div>
          <div class="form_wrapper my-3">
            <DateComponent v-model="birth_date" placeholder="Tug’ilgan sana" />
          </div>
          <div class="form_wrapper my-3">
            <SelectBox
              id="birth_region"
              v-model="birth_region"
              :options="addEmptyItemToSelectAllFilterOptions(regionsList, 'name', 'id', 'Barcha viloyatlar')"
              show-by="name"
              track-by="id"
              placeholder="Viloyatni tanlang"
              @input="fetchDistrictsByRegionId"
            />
          </div>
          <div class="form_wrapper my-3">
            <SelectBox
              id="birth_district"
              v-model="birth_district"
              :options="addEmptyItemToSelectAllFilterOptions(districtsList, 'name', 'id', 'Barcha tumanlar')"
              show-by="name"
              track-by="id"
              placeholder="Tumanni tanlang"
            />
          </div>
        </div>
      </div>
    </template>
    <template #table-content>
      <Loading v-show="loading" v-model:active="loading" :can-cancel="false" :is-full-page="true" color="#4785FE" loader="dots" />

      <TableTeenagersForSelect v-show="!loading" :table-body="juvenilesList" :view-for="'minors'" @juvenile-select="setSelectedJuvenile" />
    </template>
    <template #loading>
      <div></div>
    </template>
  </TableView>
</template>

<script setup>
import Loading from 'vue-loading-overlay';
import {defineEmits, defineProps, ref, toRefs, watch} from 'vue';
import 'vue-loading-overlay/dist/vue-loading.css';
import SelectBox from '../Common/SelectBox';
import TableView from './TableView';
import TableTeenagersForSelect from './TableTeenagersForSelect';
import useTableOptions from '../../composables/useTableOptions';
import {addEmptyItemToSelectAllFilterOptions} from '../../utils/selectboxOptionsHelper';
import useRegion from '../../composables/selectbox-list/useRegion';
import useDistrict from '../../composables/selectbox-list/useDistrict';
import useErrorAlert from '../../composables/useErrorAlert';
import DateComponent from '../Common/DateComponent';

const emit = defineEmits(['update:selectedJuvenile']);

const props = defineProps({
  fetchJuvenilesListHandler: {
    type: Function,
  },
  selectedJuvenile: {
    type: Object,
  },
  updateJuvenilesList: {
    type: Boolean,
    default: true,
  },
});
const {updateJuvenilesList} = toRefs(props);
watch(
  () => updateJuvenilesList.value,
  (updateJuvenilesList) => {
    if (updateJuvenilesList) {
      getJuvenileList();
    }
  }
);

const full_name = ref('');
const birth_date = ref('');
const birth_region = ref('');
const birth_district = ref('');

const loading = ref(true);
// const selectedJuvenileError = ref('')
const juvenilesList = ref([]);
const {alertThisError} = useErrorAlert();
const {regionsList} = useRegion();
const {districtsList, fetchDistrictsByRegionId} = useDistrict();

const {currentPage, pageSize, allPagesCount, pageSizeOptions} = useTableOptions([5, 10, 50]);

const getJuvenileList = async (params) => {
  try {
    loading.value = true;
    const resp = await props.fetchJuvenilesListHandler({
      ...params,
    });
    juvenilesList.value = resp?.results;
    allPagesCount.value = resp?.num_pages;
    currentPage.value = resp?.current;
    loading.value = false;
  } catch (e) {
    loading.value = false;
    console.log(e);
    alertThisError(e);
  }
};

function setSelectedJuvenile(juvenile) {
  emit('update:selectedJuvenile', juvenile);
}

watch(
  [pageSize, currentPage, full_name, birth_date, birth_district, birth_region],
  async ([newPageSize, newCurrentPage, newFullName, newBirthDate, newDistrict, newRegion]) => {
    await getJuvenileList({
      page: newCurrentPage,
      page_size: newPageSize,
      full_name: newFullName,
      birth_date: newBirthDate,
      birth_district: newDistrict,
      birth_region: newRegion,
    });
  },
  {
    immediate: true,
  }
);
watch(
  () => birth_region.value,
  (val) => {
    birth_district.value = null;
  }
);
</script>

